const utils = require('./utils.js');

function insertInfolinksAds() {
  if ($("#infolinkScript").length) {
    $('<script>')
    .attr('type', 'text/javascript')
    .attr('src', '//resources.infolinks.com/js/infolinks_main.js')
    .appendTo('#infolinkScript');
  }
}

function insertSidebarSkyscrapperAds() {
  if ($("#adSkyscrapper").length) {
    $('<script>')
    .attr('type', 'text/javascript')
    .attr('src', '//servedby.eleavers.com/ads/ads.php?t=MTU1Mjc7NzI1Njt2ZXJ0aWNhbC5za3lzY3JhcGVy&index=1')
    .appendTo('#adSkyscrapper');
  }
}

function insertEleaversInnerContentAds() {
  if ($('.inner-content-eleavers-250-ad').length) {
    $('.inner-content-eleavers-250-ad').each(function(){
      var adIndex = $(this).data('ad-index') || 1;
      $('<script>')
      .attr('type', 'text/javascript')
      .attr('src', '//servedby.eleavers.com/ads/ads.php?t=MTU1Mjc7NzI0ODtzcXVhcmUuc3F1YXJlX2JveA==&index=' + adIndex)
      .appendTo(this);
    });
  }

  if ($('.inner-content-eleavers-468-ad').length) {
    $('.inner-content-eleavers-468-ad').each(function(){
      var adIndex = $(this).data('ad-index') || 1;
      $('<script>')
      .attr('type', 'text/javascript')
      .attr('src', '//servedby.eleavers.com/ads/ads.php?t=MTU1Mjc7NzI5NDtob3Jpem9udGFsLmJhbm5lcg==&index=' + adIndex)
      .appendTo(this);
    });
  }

  if ($('.inner-content-eleavers-300-ad').length) {
    $('.inner-content-eleavers-300-ad').each(function(){
      var adIndex = $(this).data('ad-index') || 1;
      $('<script>')
      .attr('type', 'text/javascript')
      .attr('src', '//servedby.eleavers.com/ads/ads.php?t=MTU1Mjc7NzMwMDtzcXVhcmUubWVkaXVtX3JlY3RhbmdsZQ==&index=' + adIndex)
      .appendTo(this);
    });
  }
}

function insertAdClerksAds() {
  if ($("#ads").length) {
    function injectAdDiv(IDs) {
      var ad = $('<div/>', {'id': IDs[0], 'class': 'ad'})
      .append(
        $('<p/>', {'class': 'ad-help-text'})
        .append(
          $('<a/>', {'href': 'http://adclerks.com/marketplace/website/8954/wwe-champions-guide', 'target': '_blank', 'text': 'Advertise here for as low as $0.1 NOW! Factions ads are welcomed.'})
        )
        .append(
          $('<div/>', {'id': IDs[1]})
        )
      );
      $('#ads').append(ad);
    }

    var adURL = '';
    var adDivIDs = [];
    var viewport = utils.findBootstrapEnvironment();
    if (viewport != "xs") {
      adURL = "/js/ads/728_90_header.js";
      adDivIDs = ['adLeaderboard', 'pubclerks_3785'];
    } else {
      adURL = "/js/ads/320_50_header.js";
      adDivIDs = ['adMobileLeaderboard', 'pubclerks_3786'];
    }

    injectAdDiv(adDivIDs);
    $.getScript(adURL);
  }
}

// Function called if AdBlock is not detected
function adBlockNotDetected() {
  console.log('AdBlock is not enabled');
  $('#adBlocked')
  .removeClass('alert')
  .removeClass('alert-danger')
  .attr('role', null)
  .empty();
}

// Function called if AdBlock is detected
function adBlockDetected() {
  console.log('AdBlock is enabled');
  $('#adBlocked')
  .addClass('alert')
  .addClass('alert-danger')
  .attr('role', 'alert')
  .append(
    $('<p/>', {
      'class': 'text-center',
      'text': 'To keep the website updated frequently, please help support it by whitelisting it on your ad blocker. Thank you!'
    })
  );
}

function insertAds() {
  // insertInfolinksAds();
  insertAdClerksAds();
  insertSidebarSkyscrapperAds();
  insertEleaversInnerContentAds();
  adblockDetect(function(adblockDetected){
    if (adblockDetected) {
      adBlockDetected();
    } else {
      adBlockNotDetected();
    }
  });
}

function buildRelativeDateString(dateDiff) {
  var result = [];
  if (dateDiff.years) {
    result.push(dateDiff.years + 'y');
  }
  if (dateDiff.months) {
    result.push(dateDiff.months + 'm');
  }
  if (dateDiff.days) {
    result.push(dateDiff.days + 'd');
  }
  if (dateDiff.hours) {
    result.push(dateDiff.hours + 'h');
  }
  if (dateDiff.minutes) {
    result.push(dateDiff.minutes + 'm');
  }
  var dateString = result.join(" ");
  if (dateDiff.firstDateWasLater) {
    dateString = 'in ' + dateString;
  } else {
    dateString = dateString + ' ago';
  }
  return dateString;
}

function checkAds() {
  var adCode = utils.localStorageGetItem("ad-code");
  var adEmail = utils.localStorageGetItem("ad-email");
  var adExpires = utils.localStorageGetItem("ad-expires");
  if (adCode && adEmail && adExpires) {
    $.post("https://wwe-champions-guide-admin.herokuapp.com/adcode",
      { code: adCode },
      function(data) {
        console.log("success");
        utils.localStorageSaveItem("ad-code", data.code);
        utils.localStorageSaveItem("ad-email", data.email);
        utils.localStorageSaveItem("ad-expires", data.expires);
        $("#ads, #adSkyscrapper, #infolinkScript, iframe, .a-box, .native-placement").remove();
      })
      .fail(function(xhr) {
        console.log("error");
        utils.localStorageRemoveItem("ad-code");
        utils.localStorageRemoveItem("ad-email");
        utils.localStorageRemoveItem("ad-expires");
        insertAds();
      });
  } else {
    insertAds();
  }
}

$(document).ready(function () {
  checkAds();

  $('[data-toggle="offcanvas"]').click(function () {
    $('.row-offcanvas').toggleClass('active')
  });

  $('[data-toggle="popover"]').popover({
    animation: false
  });

  $('a[data-toggle="popover"]').on('click',function(e){
     e.preventDefault();
     return false;
   });

  $('a.date-time time, a.date-time time.relative, time.relative').each(function(){
    var publishedDatetime = moment($(this).attr('datetime'));
    var currentDatetime = moment();
    var dateDiff = moment(publishedDatetime).preciseDiff(currentDatetime, true);
    var dateString = buildRelativeDateString(dateDiff);
    $(this).text(dateString);
  });

  $(document).on('touchstart click', function (e) {
      $('[data-toggle="popover"],[data-original-title]').each(function () {
          //the 'is' for buttons that trigger popups
          //the 'has' for icons within a button that triggers a popup
          if (!$(this).is(e.target) && $(this).has(e.target).length === 0 && $('.popover').has(e.target).length === 0) {
              (($(this).popover('hide').data('bs.popover')||{}).inState||{}).click = false  // fix for BS 3.3.6
          }

      });
  });

});



